import React from 'react'
import { NavLink } from 'react-router-dom';
// import web from "./image/wer3.jpg"
import "../src/componenet/Home.css"

const Comm = (props) => {
    return (
        <>
            <section id="header" className="d-flex align-items-center">
                <div className="container-fluid">
                    <div className="row">
                        <div className="col-10 mx-auto ">
                        <div className="row">
                            <div className="col-md-6 pt-5 pt-lg-0 order-1 order-lg-1 d-flex justify-content-center flex-column" >
                                <h1>{props.name} <strong className="brand-name">Development</strong></h1>
                                <h2 className="my-1 ">
                                Your guide to summiting the peak of digital success
                                </h2>
                                <div className="mt-3">
                                    <NavLink exact  to={props.visit} className="btn-get-started">{props.btname}</NavLink>
                                </div>
                            </div>
                            <div className="col-lg-6  order-1 order-lg-2 header-img">
                                <img src={props.imgdata} className="img-fluid animated" alt="home img" />
                            </div>
                            </div>
                        </div>
                    </div>
                </div>
            </section>
        </>
    )
}

export default Comm;
